*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: 'Inter',sans-serif;
}

ul[class],
ol[class] {
  padding: 0;
  list-style: none;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* styles */


body{
  background-color: rgb(222 244 125);
}
.App {
  margin-top: 100px;
  max-width: 700px;
  width: 100%;
  margin: 100px auto 0;
  padding: 0 10px;
}

.title {
  text-align: center;
  color: #fff;
  font-size: 50px;
  line-height: 60px;
}

.form {
  display: flex;
  height: 55px;
  margin-top: 50px;
}

.todo_input {
  background-color: #fff;
  font-size: 22px;
  line-height: 26px;
  border: 1px solid #fff;
  padding: 0 5px;
  outline: none;
  max-width: 529px;
  height: 55px;
  width: 100%;
  border-radius: 10px 0 0 10px;
}

.todo_input::placeholder {
  color: rgb(123, 123, 123);
  font-size: 18px;
  line-height: 22px;
}

.todo_button {
  outline: none;
  border: none;
  background-color: #fff;
  cursor: pointer;
  margin-right: 10px;
  width: 50px;
  border-radius: 0 10px 10px 0;
}

.todo_button img {
  background-color: transparent;
}

.select select {
  height: 100%;
  background-color: #fff;
  outline: none;
  border: none;
  padding: 0 5px;
  border-radius: 10px;
  cursor: pointer;
}

.select option {
  background-color: #fff;
}

.todo_item.completed {
  text-decoration: line-through;
  color: green;
}

.todo_container {
  margin-top: 60px;
}

.todo {
  display: flex;
  margin: 0 auto;
}

.todo li {
  width: 100%;
  background-color: #fff;
  padding: 12px 10px;
  font-size: 22px;
  line-height: 26px;
  border-radius: 10px;
  margin-right: 10px;
}

.todo + .todo {
  margin-top: 5px;
}

.complete_btn {
  border: none;
  background-color: rgb(76, 201, 76);
  cursor: pointer;
  transition: all 0.3s;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 55px;
  margin-right: 5px;
}

.complete_btn img {
  background-color: transparent;
}
.complete_btn:hover {
  background-color: rgb(59, 180, 59);
}

.trash_btn {
  border: none;
  background-color: rgb(246, 79, 79);
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  width: 55px;
}

.trash_btn img {
  background-color: transparent;
}

.trash_btn:hover {
  background-color: rgb(247, 50, 50);
}
@media(max-width: 520px) {
  .title{
    font-size: 38px;
    line-height: 44px;
  }
}

@media(max-width: 420px) {
  .todo li {
    font-size: 18px;
    line-height: 22px;
  }
  .todo_input {
    font-size: 16px;
    line-height: 18px;
  }
}

@media(max-width: 370px) {
    .todo li {
      font-size: 16px;
      line-height: 18px;
    }
}